@use "./variables" as *;
@use "./mixins" as *;
@use "./animations" as *;

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

// resets
body {
  margin: 0;
  padding: 0;
  line-height: 1.3;
  font-family: "Public Sans", sans-serif;
  font-size: 1.125rem;
  font-weight: 300;
  color: $grayishBlue;

  @include breakpoint-up(l) {
    font-size: $font-l;
  }
}
//  headings
h1,
h2,
h3 {
  margin: 0;
  font-weight: 300;
  line-height: 1.15;
  color: $darkBlue;
}

h1 {
  font-size: 2.3125rem;
  margin-bottom: 1.5rem;

  @include breakpoint-up(l) {
    font-size: $font-xl;
  }
}

h2 {
  font-size: 1.875rem;
  margin-bottom: 1.5625rem;

  @include breakpoint-up(md) {
    font-size: 2.25rem;
    margin-bottom: 2.25rem;
  }
}

p {
  line-height: 1.5;
  margin: 0 0 2.25rem;
}
// links

a,
a:visited,
a:hover {
  text-decoration: none;
  font-weight: 400;
}

// buttons
button,
.button {
  display: inline-block;
  font-size: $font-sm;
  text-transform: capitalize;
  padding: 0.875rem 2.1875rem;
  background: linear-gradient(to right, $limeGreen, $brightCyan);
  border-radius: 50px;
  cursor: pointer;
  border: 0;
  color: $white;
}
// flex
.flex {
  display: flex;

  &-jc-sb {
    justify-content: space-between;
  }
  &-jc-c {
    justify-content: center;
  }
  &-ai-c {
    align-items: center;
  }
}

// visibility
.hide-in-mobile {
  @include breakpoint-down(md) {
    display: none;
  }
}

.hide-in-desktop {
  @include breakpoint-up(md) {
    display: none;
  }
}

// animation
.fade-in {
  animation: fade-in 300ms ease-in-out forwards;
}
.fade-out {
  animation: fade-out 300ms ease-in-out forwards;
}

.container {
  max-width: 69.375rem;
  margin: 0 auto;

  &--pall {
    padding: 4.375rem 1.5rem;
  }

  &--py {
    padding: 4.375rem 0;
  }

  &--px {
    padding: 0 1.5rem;
  }
  &--pt {
    padding-top: 4.375rem;
  }

  &--pb {
    padding-bottom: 4.375rem;
  }

  &--pl {
    padding-left: 1.5rem;
  }

  &--pr {
    padding-right: 1.5rem;
  }

  @include breakpoint-up(ml) {
    &--pall {
      padding-top: 6rem;
      padding-bottom: 6rem;
    }
    &--py {
      padding: 6rem 0;
    }
    &--pt {
      padding-top: 6rem;
    }

    &--pb {
      padding-bottom: 6rem;
    }
  }
}
